.youTube {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  background-image: url('./../images/youTube/modal-youtube-mobile.jpg');
  background-position: top center;
  background-attachment: fixed;

  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media (min-width: 600px) {
    background-image: url('./../images/youTube/кінотеатер.jpg');
    background-position: center;
    background-attachment: fixed;
  }
}

.youTube__window {
  max-width: 780px;
  height: 100vh;
  padding: 0 20px;
  margin: 16px auto;
  justify-content: center;
  align-items: center;

  @media (min-width: 560px) {
    margin: 70px auto;
  }
}

.youTub__video {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
}

.youTub__video iframe {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.svg__closed {
  position: absolute;
  z-index: 1;
  width: 70px;
  height: 40px;
  cursor: pointer;
}

.modal__btn-door-clocsed {
  top: 3px;
  right: -10px;
  fill: #ff6b08;
}

.modal__btn-door-exit {
  display: none;
  top: 6px;
  right: -16px;
  fill: #21ae2a;

  transition: display 0.3s;
}

.div__hover:hover .modal__btn-door-exit {
  display: block;
}

.div__hover:hover .modal__btn-door-clocsed {
  display: none;
}
