body.modal-open {
  overflow: hidden;
}

img {
  margin: 0;
  padding: 0;
}

.backdrop {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.8);
  transition: opacity 250ms $timing-function;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .footer-modal {
  transform: translate(-50%, -50%) scale(0.9);
  opacity: 0;
}

.footer-modal {
  z-index: 10;
  position: relative;
  padding: 48px 20px 40px;
  transform: scale(1);
  transition: transform 250ms $timing-function, opacity 250ms $timing-function;

  background-color: $primary-brg-color;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  //   @media screen and (min-width: 320px) {
  //     min-width: 280px;
  //   }

  @media screen and (min-width: 480px) {
    width: 450px;
    margin-right: auto;
    margin-left: auto;
  }

  @media screen and (min-width: 768px) {
    min-width: 704px;
    min-height: 552px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
  }

  @media screen and (min-width: 1200px) {
    min-width: 806px;
    min-height: 558px;
  }
}

.footer-modal__close-btn {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 8px 8px 0 2px;
  padding: 0;

  border: none;
  cursor: pointer;
  background-color: $primary-brg-color;
}

.footer-modal__icon-close {
  fill: $primary-text-color;
  background-color: $primary-brg-color;
  transition: fill 250ms $timing-function;

  &:hover,
  &:focus {
    fill: $btn-accent-hover-color;
  }
}

.swiper {
  margin-top: 60px;
  margin-left: 0;
  margin-right: 0;
  width: 650px;
  height: 450px;
}

